<template>
    <div style="margin:35px">
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder="" @keyup.enter.native="getGlobalHost"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="getGlobalHost"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="handleAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <!---->
        <el-table :data="project" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
            <el-table-column type="selection" min-width="5%">
            </el-table-column>
            <el-table-column prop="name" label="" min-width="15%" sortable show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="host" label="HOST" min-width="28%" sortable show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="description" label="" min-width="27%" sortable show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="status" label="" min-width="10%" sortable>
                <template slot-scope="scope">
                    <img v-show="scope.row.status" src="../../../assets/icon-yes.svg"/>
                    <img v-show="!scope.row.status" src="../../../assets/icon-no.svg"/>
                </template>
            </el-table-column>
            <el-table-column label="" min-width="15%">
                <template slot-scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
                    <el-button type="info" size="small" @click="handleChangeStatus(scope.$index, scope.row)">{{scope.row.status===false?'':''}}</el-button>
                </template>
            </el-table-column>
        </el-table>

        <!---->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
            <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :page-count="total" style="float:right;">
            </el-pagination>
        </el-col>

        <!---->
        <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false" style="width: 60%; left: 20%">
            <el-form :model="editForm"  :rules="editFormRules" ref="editForm" label-width="80px">
                <el-form-item label="" prop="name">
                    <el-input v-model="editForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="Host" prop='host'>
                    <el-input v-model="editForm.host" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop='description'>
                    <el-input type="textarea" :rows="7" v-model="editForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false"></el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
            </div>
        </el-dialog>

        <!---->
        <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false" style="width: 60%; left: 20%">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-form-item label="" prop="name">
                    <el-input v-model="addForm.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="Host" prop='host'>
                    <el-input v-model="addForm.host" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop='description'>
                    <el-input type="textarea" :rows="7" v-model="addForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false"></el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    //import NProgress from 'nprogress'
    import { test } from '../../../api/api'
    import $ from 'jquery'
    export default {
        data() {
            return {
                filters: {
                    name: ''
                },
                project: [],
                total: 0,
                page: 1,
                listLoading: false,
                sels: [],//

                editFormVisible: false,//
                editLoading: false,
                editFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
                    ],
                    host: [
                        { required: true, message: 'host', trigger: 'blur' },
                    ],
                    description: [
                        { required: false, message: '', trigger: 'blur' },
                        { max: 1024, message: '1024', trigger: 'blur' }
                    ]
                },
                //
                editForm: {
                    name: '',
                    host: '',
                    description: ''
                },

                addFormVisible: false,//
                addLoading: false,
                addFormRules: {
                    name: [
                        { required: true, message: '', trigger: 'blur' },
                        { min: 1, max: 50, message: ' 1  50 ', trigger: 'blur' }
                    ],
                    host: [
                        { required: true, message: 'host', trigger: 'blur' },
                    ],
                    description: [
                        { required: false, message: '', trigger: 'blur' },
                        { max: 1024, message: '1024', trigger: 'blur' }
                    ]
                },
                //
                addForm: {
                    name: '',
                    host: '',
                    description: ''
                }

            }
        },
        methods: {
            // HOST
            getGlobalHost() {
                this.listLoading = true;
                let self = this;
                $.ajax({
                    type: "get",
                    url: test+"/api/global/host_total",
                    async: true,
                    data: { project_id: this.$route.params.project_id, page: self.page, name: self.filters.name},
                    headers: {
                        Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                    },
                    timeout: 5000,
                    success: function(data) {
                        self.listLoading = false;
                        if (data.code === '999999') {
                            self.total = data.data.total;
                            self.project = data.data.data
                        }
                        else {
                            self.$message.error({
                                message: data.msg,
                                center: true,
                            })
                        }
                    },
                })
            },
            //
            handleDel: function (index, row) {
                this.$confirm('?', '', {
                    type: 'warning'
                }).then(() => {
                    this.listLoading = true;
                    //NProgress.start();
                    let self = this;
                    $.ajax({
                        type: "post",
                        url: test+"/api/global/del_host",
                        async: true,
                        data: {project_id: this.$route.params.project_id, ids: row.id},
                        headers: {
                            Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                        },
                        timeout: 5000,
                        success: function(data) {
                            if (data.code === '999999') {
                                self.$message({
                                    message: '',
                                    center: true,
                                    type: 'success'
                                })
                            } else {
                                self.$message.error({
                                    message: data.msg,
                                    center: true,
                                })
                            }
                            self.getGlobalHost()
                        },
                    })

                }).catch(() => {
                });
            },
            handleChangeStatus: function(index, row) {
                let self = this;
                this.listLoading = true;
                if (row.status) {
                    $.ajax({
                        type: "post",
                        url: test+"/api/global/disable_host",
                        async: true,
                        data: { project_id: this.$route.params.project_id, host_id: row.id},
                        headers: {
                            Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                        },
                        timeout: 5000,
                        success: function(data) {
                            self.listLoading = false;
                            if (data.code === '999999') {
                                self.$message({
                                    message: '',
                                    center: true,
                                    type: 'success'
                                })
                                row.status = !row.status;
                            }
                            else {
                                self.$message.error({
                                    message: data.msg,
                                    center: true,
                                })
                            }
                        },
                    })
                } else {
                    $.ajax({
                        type: "post",
                        url: test+"/api/global/enable_host",
                        async: true,
                        data: { project_id: this.$route.params.project_id, host_id: row.id},
                        headers: {
                            Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                        },
                        timeout: 5000,
                        success: function(data) {
                            self.listLoading = false;
                            if (data.code === '999999') {
                                self.$message({
                                    message: '',
                                    center: true,
                                    type: 'success'
                                });
                                row.status = !row.status;
                            }
                            else {
                                self.$message.error({
                                    message: data.msg,
                                    center: true,
                                })
                            }
                        },
                    })
                }
            },
            handleCurrentChange(val) {
                this.page = val;
                this.getGlobalHost()
            },
            //
            handleEdit: function (index, row) {
                this.editFormVisible = true;
                this.editForm = Object.assign({}, row);
            },
            //
            handleAdd: function () {
                this.addFormVisible = true;
            },
            //
            editSubmit: function () {
                let self = this;
                this.$refs.editForm.validate((valid) => {
                    if (valid) {
                        this.$confirm('', '', {}).then(() => {
                            self.editLoading = true;
                            //NProgress.start();
                            $.ajax({
                                type: "post",
                                url: test+"/api/global/update_host",
                                async: true,
                                data: { project_id: this.$route.params.project_id,
                                    host_id: self.editForm.id,
                                    name: self.editForm.name,
                                    host: self.editForm.host,
                                    description: self.editForm.description },
                                headers: {
                                    Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                                },
                                timeout: 5000,
                                success: function(data) {
                                    self.editLoading = false;
                                    if (data.code === '999999') {
                                        self.$message({
                                            message: '',
                                            center: true,
                                            type: 'success'
                                        });
                                        self.$refs['editForm'].resetFields();
                                        self.editFormVisible = false;
                                        self.getGlobalHost()
                                    } else if (data.code === '999997'){
                                        self.$message.error({
                                            message: data.msg,
                                            center: true,
                                        })
                                    } else {
                                        self.$message.error({
                                            message: data.msg,
                                            center: true,
                                        })
                                    }
                                },
                            })
                        });
                    }
                });
            },
            //
            addSubmit: function () {
                this.$refs.addForm.validate((valid) => {
                    if (valid) {
                        let self = this;
                        this.$confirm('', '', {}).then(() => {
                            self.addLoading = true;
                            //NProgress.start();
                            $.ajax({
                                type: "post",
                                url: test+"/api/global/add_host",
                                async: true,
                                data: { project_id: this.$route.params.project_id,
                                    name: self.addForm.name,
                                    host: self.addForm.host,
                                    description: self.addForm.description },
                                headers: {
                                    Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                                },
                                timeout: 5000,
                                success: function(data) {
                                    self.addLoading = false;
                                    if (data.code === '999999') {
                                        self.$message({
                                            message: '',
                                            center: true,
                                            type: 'success'
                                        });
                                        self.$refs['addForm'].resetFields();
                                        self.addFormVisible = false;
                                        self.getGlobalHost()
                                    } else if (data.code === '999997'){
                                        self.$message.error({
                                            message: data.msg,
                                            center: true,
                                        })
                                    } else {
                                        self.$message.error({
                                            message: data.msg,
                                            center: true,
                                        });
                                        self.$refs['addForm'].resetFields();
                                        self.addFormVisible = false;
                                        self.getGlobalHost()
                                    }
                                },
                            })
                        });
                    }
                });
            },
            selsChange: function (sels) {
                this.sels = sels;
            },
            //
            batchRemove: function () {
                let ids = this.sels.map(item => item.id).toString();
                let self = this;
                this.$confirm('', '', {
                    type: 'warning'
                }).then(() => {
                    self.listLoading = true;
                    //NProgress.start();
                    $.ajax({
                        type: "post",
                        url: test+"/api/global/del_host",
                        async: true,
                        data: {project_id: this.$route.params.project_id, ids: ids},
                        headers: {
                            Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))
                        },
                        timeout: 5000,
                        success: function(data) {
                            self.listLoading = false;
                            if (data.code === '999999') {
                                self.$message({
                                    message: '',
                                    center: true,
                                    type: 'success'
                                })
                            }
                            else {
                                self.$message.error({
                                    message: data.msg,
                                    center: true,
                                })
                            }
                            self.getGlobalHost()
                        },
                    })
                }).catch(() => {

                });
            }
        },
        mounted() {
            this.getGlobalHost();

        }
    }

</script>
<style>
</style>



